  body {
    max-width: 392px;
    min-width: 320px;
  }
  .socialBox {
    position: absolute;
    bottom: 10vh;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    text-align: center
  }
}
